button{
    background-color: rgb(30, 113, 162);
    color: white;
    padding: 10px;
    border: none;
    cursor: pointer;
    margin: 10px;
}
li{
    list-style: none;
    margin: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    box-shadow: 7px 7px 7px #ccc;
    width: 200px;
    display: inline-block;
    
}
ul{
    padding: 0;
}
div{
    margin: 10px;
    background-color: rgb(232, 232, 232);
    color: #000000;
}
h1{
    color: rgb(0, 0, 0);
    font-size: 20px;
    text-align: center;
}
span{
    color: rgb(251, 5, 5);
    font-size: 12px;
    text-align: center;
}